import React, { FC, useEffect, useState, useRef } from 'react';
import styled from 'styled-components';
import fp from 'lodash/fp';
import { useWindowSize } from './useWindowSize';
import { getElementSize } from '../utils';

const WrapperEl = styled.div`
  position: fixed;
  top: ${({ y }) => y && `${y}px`};
  left: ${({ x }) => x && `${x}px`};
`;

export const Draggable = (Component: FC) => {

  const Wrapper = (props: IDraggableProps) => {
    const { id, setSelection } = props;
    const [coordinates, setCoordinates] = useState([0, 0]);
    const [dragStartCoordinates, setDragStartCoordinates] = useState([0, 0]);
    const [move, setMove] = useState(false);
    const windowSize = useWindowSize();
    const componentRef = useRef(null);
    useEffect(() => {
      if (componentRef.current) {
        fp.flow([
          getElementSize,
          fp.tap(console.log),
          ([height, width]) => [(windowSize.width - width) / 2, (windowSize.height - height) / 2],
          setCoordinates
        ])(componentRef, windowSize);
      }
    }, [componentRef.current]);
    const onMouseDown = fp.flow([
      (event) => setDragStartCoordinates([event.clientX, event.clientY]),
      () => setSelection(id),
      () => setMove(true)
    ]);
    const onMouseUp = fp.flow([
      () => setMove(false)
    ]);
    const onMouseMove = fp.flow([
      // (event) => [event.clientX, event.clientY, event.clientX - dragStartCoordinates[0], event.clientY - dragStartCoordinates[1]],
      // ([clientX, clientY, x, y]) => setDragStartCoordinates([clientX, clientY]) && [x, y],
      (event) => [event.movementX, event.movementY],
      ([x, y]) => setCoordinates([coordinates[0] + x, coordinates[1] + y])
    ]);
    const componentProps = {
      ...props,
      onMouseUp,
      onMouseDown,
      onMouseMove: move ? onMouseMove : () => { }
      // onDrag
    }
    return (
      <WrapperEl ref={ componentRef } style={ { top: `${coordinates[1]}px`, left: `${coordinates[0]}px` } }>
        <Component { ...componentProps } />
      </WrapperEl >
    );
  }
  return Wrapper;
}
